






































































































































import Vue from "vue";
import ColorConvert from "@/util/colorConvert.ts";

export default Vue.extend({
    components: {
    },
    props: {
        responsive: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            color: new ColorConvert(),
            hexT: "000000",
            setting: {
                mode: 0,
                modeOption: [
                    {label: "RGB", value: 0},
                    {label: "HSL", value: 1},
                    {label: "HSV", value: 2},
                ]
            },
            screenSize: document.body.clientWidth < 768 ? "small" : "large"
        }
    },
    computed: {
        red: {
            get: function (): number {
                return this.color.red;
            },
            set: function (val: number) {
                this.color.red = val;
            }
        },
        green: {
            get: function (): number {
                return this.color.green;
            },
            set: function (val: number) {
                this.color.green = val;
            }
        },
        blue: {
            get: function (): number {
                return this.color.blue;
            },
            set: function (val: number) {
                this.color.blue = val;
            }
        },
        hex: {
            get: function (): string {
                this.hexT = this.color.hex;
                return this.color.hex;
            },
            set: function (val: string) {
                this.color.hex = val;
            }
        },
        hue: {
            get: function(): number {
                return this.color.hue;
            },
            set: function(val: number) {
                this.color.hue = val;
            }
        },
        sHSL: {
            get: function (): number {
                return this.color.sHSL;
            },
            set: function (val: number) {
                this.color.sHSL = val;
            }
        },
        lHSL: {
            get: function (): number {
                return this.color.lHSL;
            },
            set: function (val: number) {
                this.color.lHSL = val;
            }
        },
        sHSV: {
            get: function ():number {
                return this.color.sHSV;
            },
            set: function (val: number) {
                this.color.sHSV = val;
            }
        },
        vHSV: {
            get: function (): number {
                return this.color.vHSV;
            },
            set: function (val: number) {
                this.color.vHSV = val;
            }
        },
    },
    watch: {
        hexT(val) {
            if(/^[0-9a-f]{6}$/i.test(val)) {
                this.$data.color.hex = val;
            }
        }
    },
    methods: {
        resizeHandle (): void {
            this.screenSize = document.body.clientWidth < 768 ? "small" : "large";
        }
    },
    mounted (): void {
        window.addEventListener("resize", this.resizeHandle);
    },
    beforeDestroy(): void {
        window.removeEventListener("resize", this.resizeHandle);
    }

})
